  methods: {
    /**
     * @description  提交
    */
    submission() {
      this.$toast.loading({
        forbidClick: true,
        message: '加载中...',
        duration: 0
      })
      const myreg = /^[1][3,4,5,7,8][0-9]{9}$/
      // 手机号判断
      if (this.telephone === '') {
        this.$toast('请输入手机号')
        return false
      }
      if (this.telephone.includes('****') || myreg.test(this.telephone)) {

      } else {
        this.$toast.fail('请填写正确联系电话')
        return false
      }
      // picUrl图片路径处理
      var context = this
      // 图片存在
      if (context.images.length > 0) {
        this.loading = true
        personalApi.uploadPictureBybase64({
          base64s: context.images.map(img => img.content.split(',')[1])
        }).then(imgRes => {
          context.loading = false
          if (imgRes.data.success) {
            const data = {
              repairSn: context.repairSn.repairSn, // 逆向单号
              description: context.content, // 描述
              mobile: context.telephone, // 用户手机号
              picUrl: imgRes.data.data.fileList.map(img => img.imgUrl).join() // 图片url 最多6张 英文逗号分隔
            }
            personalApi.requestXiaoer(data).then(res => {
              if (res.data.success) {
                context.$toast.clear()
                // context.service = true;
                context.$dialog.confirm({
                  message: '您的订单已邀请小二介入，请保持电话通畅，我们将48小时之内告知您处理结果',
                  className: 'dialog-custom',
                }).then(() => {
                  context.$router.replace({ path: '/details', query: { orderProductId: context.repairSn.orderProductId } })
                  context.$router.go(-1)
                }).catch(() => {
                  context.$router.replace({ path: '/details', query: { orderProductId: context.repairSn.orderProductId } })
                  context.$router.go(-1)
                });
              } else {
                context.$toast.fail(res.data.message)
              }
            })
          } else {
            context.$toast(imgRes.data.message)
          }
        }).catch(() => {
          this.loading = false
          context.$toast.fail('上传图片失败')
        })
      } else {
        const data = {
          repairSn: context.repairSn.repairSn, // 逆向单号
          description: context.content, // 描述
          mobile: context.telephone, // 用户手机号
        }
        personalApi.requestXiaoer(data).then(res => {
          if (res.data.success) {
            // context.service = true;
            context.$toast.clear()
            context.$dialog.confirm({
              message: '您的订单已邀请小二介入，请保持电话通畅，我们将48小时之内告知您处理结果',
              className: 'dialog-custom',
            }).then(() => {
              context.$router.replace({ path: '/details', query: { orderProductId: context.repairSn.orderProductId } })
              context.$router.go(-1)
            }).catch(() => {
              context.$router.replace({ path: '/details', query: { orderProductId: context.repairSn.orderProductId } })
              context.$router.go(-1)
            });
          } else {
            context.$toast.fail(res.data.message)
          }
        })
      }
    },
    /**
     * @description  弹窗确定回调
    */
    onConfirm() {
      this.service = false;
      this.$router.replace({ path: '/details', query: { orderProductId: this.repairSn.orderProductId } })
      this.$router.go(-1)
    },
    /**
     * @description  弹窗取消回调
    */
    onCancel() {
      this.service = false
    },
